<template>
  <div class="page-container">
    <div class="page-box">
      <div class="page-box-title">
        <h2>Admin</h2>
      </div>
      <div class="page-box-content">
        <h4>Result</h4>
        <button @click="test">test</button>
        <div class="content">
          <ul>
            <li
              v-for="roomTypeOption in allRoomTypeOptions"
              :key="roomTypeOption.id"
            >
              {{roomTypeOption.id}}:
              <ul>
                <li
                  v-for="value in roomTypeOption.values"
                  :key="value.volume"
                >{{value.volume}}: {{value.rt60}}</li>
              </ul>
            </li>
          </ul>
        </div>
      </div>
      <div class="page-box-footer"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Admin",
  computed: {
    allRoomTypeOptions: function() {
      return this.$store.getters.getAdvicevaluesList;
    }
  },
  methods: {
    test() {}
  }
};
</script>


